<template>
<div id="dealut">
    <div class="fromtok">
        <h1>感谢收看我的简历</h1>
        <div class="token">
            <input type="text" placeholder="请输入token" v-model="value">
        </div>
        <div class="btn" @click="addsub">提交</div>
    </div>
</div>
</template>

<script>
import {
    reactive,
    toRefs
} from 'vue'
import {
    useStore
} from 'vuex'
import {
    useRouter
} from 'vue-router'
export default {
    name: 'dealut',
    setup() {
        const store = useStore()
        const router = useRouter()
        const state = reactive({
            value: ''
        })
        const addsub = () => {
            store.commit('getValue', state.value)
            router.push('/home')
        }
        return {
            ...toRefs(state),
            addsub
        }
    }
}
</script>

<style lang="scss">
#dealut {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    background: url("../assets/banner01.jpg") no-repeat center center;
    background-size: cover;

    .fromtok {
        width: 50%;
        height: 50%;

        h1 {
            text-align: center;
            color: white;
            font-size: 40px;
        }

        .token {
            display: flex;
            justify-content: center;
            margin: 0 auto;
            margin-top: 30px;

            input {
                width: 60%;
                height: 50px;
                color: white;
                outline: none;
                padding: 10px;
                background-color: transparent;
                border: 1px solid rgba(255, 255, 255, .5);
                border-radius: 10px;
            }
        }

        .btn {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 20%;
            height: 10%;
            margin: 0 auto;
            margin-top: 20px;
            background-color: #fff;
            color: white;
            font-weight: 600;
            background-color: transparent;
            border: 1px solid rgba(255, 255, 255, .5);
            border-radius: 20px;
            cursor: pointer;
        }
    }
}

@media screen and(max-width: 1200px) {
    #dealut {
        .fromtok {
            h1 {
                font-size: 35px;
            }
        }
    }
}

@media screen and(max-width: 1000px) {
    #dealut {
        .fromtok {
            .token {
                input {
                    width: 80%;
                }
            }
        }
    }
}

@media screen and(max-width: 750px) {
    #dealut {
        .fromtok {
            width: 80%;

            .token {
                input {
                    width: 80%;
                }
            }
        }
    }
}

@media screen and(max-width: 500px) {
    #dealut {
        .fromtok {
            width: 80%;

            .token {
                input {
                    width: 100%;
                }
            }

            .btn {
                width: 25%;
                font-size: 12px;
            }
        }
    }
}
</style>
