import index from '../views/Index.vue'
import dealut from '../views/Dealut.vue'
import meun from '../script/meun'

const routes  = [
    {
        path: '/',
        name:'index',
        redirect:'/home',
        component: index,
        children:meun
    },
    {
        path: '/dealut',
        name: 'dealut',
        component:dealut
    }
]

export default routes