import {createStore} from 'vuex'
export default createStore({
    state: {
        token:'',
        dealutoken:'200324'
    },
    getters: {
        getToken(state) {
            return state.token
        },
        getdealutoken(state) {
            return state.dealutoken
        }
    },
    mutations: {
        getValue(state,value) {
            state.token=value
        }
    }
})