<template>
<div>
    <nav>
        <div class="wrap">
            <div class="nav-left">
                许衍
            </div>
            <div class="nav-right" :class="activeShow?'nav-right-active':''">
                <ul class="menu" :class="activeShow?'menu-active':''">
                    <li v-for="(item,index) in meun" :key="item.name" @click="jia(index)">
                        <router-link :to="item.path" :class="currIndex==index?'menu-hover':''">{{item.name}}</router-link>
                    </li>
                </ul>
            </div>
            <img src="../assets/dh.png" alt="" class="dh" @click="down">
        </div>
    </nav>
    <router-view></router-view>
</div>
</template>

<script>
import meun from '../script/meun'
import {
    reactive,
    ref,
    toRefs
} from 'vue'
import {
    useStore
} from 'vuex'
import {
    useRouter
} from 'vue-router'
export default {
    name: 'Index',
    data() {
        return {
            meun
        }
    },
    setup() {
        let num = ref(0)
        let currIndex = ref(0)
        const store = useStore()
        const route = useRouter()
        const state = reactive({
            activeShow: false
        })
        const down = () => {
            if (num.value % 2 == 0) {
                state.activeShow = true
            } else {
                state.activeShow = false
            }
            num.value++
        }
        const jia = (index) => {
            currIndex.value = index
            state.activeShow = false
            num.value++
        }
        return {
            num,
            down,
            currIndex,
            jia,
            store,
            route,
            ...toRefs(state)
        }
    },
    beforeRouteEnter(to, from, next) {
        next(vm => {
            if (vm.store.getters.getToken == vm.store.getters.getdealutoken) {
                next()
            } else {
                vm.route.push('/dealut')
            }
        })
    }
}
</script>

<style lang="scss">
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
}

.wrap {
    max-width: 1120px;
    padding: 0 20px;
    margin: 0 auto;
}

.public {
    display: block !important;
}

nav {
    position: fixed;
    right: 0;
    left: 0;
    z-index: 99;
    height: 50px;
    line-height: 50px;
    background: rgba(255, 255, 255, .1);
    transition: 1s all ease;

    .wrap {
        position: relative;
        display: flex;
        height: 50px;
        justify-content: space-between;
        align-items: center;
        flex-wrap: nowrap;

        .nav-left {
            color: white;
            font-size: 18px;
            letter-spacing: 5px;
        }

        .nav-right {
            .menu {
                overflow: hidden;

                li {
                    float: left;

                    a {
                        display: block;
                        padding: 15px;
                        line-height: 20px;
                        font-size: 14px;
                        font-weight: 500;
                        color: white;

                        &:hover {
                            color: #A7E615;
                        }
                    }
                }
            }
        }

        img {
            display: none;
            position: absolute;
            right: 20px;
            width: 30px;
            height: 30px;
            vertical-align: middle;
        }

        .nav-right-active {
            z-index: 3;
        }

        .menu-active {
            height: 250px !important;
        }

        .menu-hover {
            color: #A7E615 !important;
        }
    }
}

@media screen and (max-width:750px) {
    nav {
        .wrap {
            .nav-right {
                position: absolute;
                left: 0;
                bottom: -250px;
                width: 100%;
                z-index: -1;
                background: rgba(255, 255, 255, .1);

                .menu {
                    height: 0;
                    transition: all 1s;

                    li {
                        float: none;
                    }
                }
            }

            img {
                display: block;
                cursor: pointer;
            }
        }
    }
}
</style>
